import { Router } from 'vue-router'
import $store from '@/store'
import { PublicApi, UserApi } from '@/apis'

declare module 'vue-router' {
  interface RouteMeta {
    // 
    isAdmin?: boolean
    // 
    requireLogin?: boolean
    // title
    title?: string
  }
}

function registerRouteGuard(router: Router) {
  /**
     * 
     */
  router.beforeEach((to, from) => {
    // PV
    const { fullPath } = to
    PublicApi.reportPv(fullPath)

    // title
    window.document.title = `${import.meta.env.VITE_APP_TITLE} ${to.meta.title}`

    // if (to.meta.requireLogin) {
    //   if (from.path === '/') {
    //     return from
    //   }
    //   return false
    // }
    return true
  })

  /**
  * 
  */
  router.beforeResolve(async (to) => {
    if (to.meta.isAdmin) {
      try {
        const isAdmin = (await UserApi.checkPower()).data.power
        $store.commit('user/setSuperAdmin', isAdmin)
        return isAdmin || {
          name: '404',
        }
      } catch (error) {
        // if (error instanceof NotAllowedError) {
        //     // ... 
        //     return false
        // } else {
        //     // 
        //     throw error
        // }
        console.error(error)
        return false
      }
    }
    return true
  })

  /**
     * 
     */
  router.afterEach((to, from, failure) => {
    // ,
    // sendToAnalytics(to.fullPath)
    if (failure) {
      console.error(failure)
    }
  })
}

export default registerRouteGuard
